<template>
  <div>
    <!-- <nav><router-link class="nav-item" :to="{ name: 'search'}" >Back to Search</router-link></nav> -->
    <nav id="link-section">
      <router-link class :to="{ name: 'search' }"
        ><span class="main-link">Back to Search</span></router-link
      >
    </nav>
    <div v-if="hasStatusMsg">
      <message-pane />
    </div>
    <div v-if="isLoading">
      <img src="@/assets/Fight_for_the_net.gif" />
    </div>
    <business-details :businessInfo="businessInfo" v-if="!isLoading" />
  </div>
</template>

<script>
import BusinessDetails from "../components/BusinessDetails.vue";
import fusionService from "@/services/FusionService";
import MessagePane from "../components/MessagePane.vue";
export default {
  components: {
    BusinessDetails,
    MessagePane,
  },
  data() {
    return {
      businessInfo: null,
      isLoading: true,
    };
  },
  computed: {
    hasStatusMsg() {
      return this.$store.state.statusMsg;
    },
  },
  created() {
    fusionService
      .getBusinessDetails(this.$route.params.businessId)
      .then((resp) => {
        this.businessInfo = resp.data;
        this.isLoading = false;
      })
      .catch((error) => {
        if (error.response && error.response.status === 404) {
          this.notFound = true;
          this.isLoading = false;
          this.$store.commit("SET_ERROR_MSG", "Business not found.");
          this.$store.commit("SET_MESSAGE_INFO", {
            message: "Business not found.",
            type: "ERROR",
          });
        } else if (error.code && error.code === "ECONNABORTED") {
          this.$store.commit("SET_MESSAGE_INFO", {
            message: "Unable to connect to data server.",
            type: "ERROR",
          });
          this.isLoading = false;
        }
      });
  },
};
</script>

<style>
nav {
  text-align: center;
  margin-bottom: 10px;
}
.nav-item {
  margin-bottom: 20px;
  font-family: "Rubik Bubbles", Arial, Helvetica, sans-serif;
  /* color: blue; */
  text-decoration: none;
}
</style>