<template>
  <div
    :class="{ error: msgType == 'ERROR', success: msgType != 'ERROR' }"
    id="msgPane"
  >
    {{ msg }}
  </div>
</template>

<script>
export default {
  name: "message-pane",
  computed: {
    msgType() {
        return this.$store.state.statusType;
    },
    msg() {
        return this.$store.state.statusMsg;
      }
    },
};
</script>

<style>
.success {
  background-color: green;
  height: auto;
  width: auto;
  padding: 10px;
  margin: 10px;
}

.error {
  background-color: darkred;
  height: auto;
  width: auto;
  padding: 10px;
  margin: 10px;
}

#msgPane {
  text-align: center;
}
</style>