<template>
  <div>
  <div class="info-section" :style="cssVars">
    <div class="detail-section">
      <div id="name-info" class="info-field bold">
        <div id="name-text">
        {{ businessInfo.name }} </div> <div  id="mark-icon" class="bookmark" @click="toggleBookmark">
            <span v-show="!isBookmarked"><i class="fa-regular fa-bookmark"></i></span>
            <span v-show="isBookmarked" class="green"><i class="fa-solid fa-bookmark"></i></span>
            </div>
      </div>
      <div  id="address-info" class="info-field">
        {{ businessInfo.location.address1 }}
      </div>
      <div  id="city-zip-info" class="info-field">
        {{ businessInfo.location.city }}, {{ businessInfo.location.state }}
        {{ businessInfo.location.zipCode }}
      </div>

      <div  id="phone-info" class="info-field" v-if="businessInfo.phone">
        {{ formatttedPhone }}
      </div>

      <div  id="not-found" class="info-field info-is-closed bold" v-if="businessInfo.is_closed">
        BUSINESS IS PERMANETLY CLOSED
      </div>
    </div>
  </div>

  </div>
</template>

<script>

import userService from '@/services/UserService'
export default {
  name: "business-details",
  props: ["businessInfo"],
  components: {
  },
  data() {
      return {
          bookmarked: false
      }
  },
  computed: {
    formatttedPhone() {
      const phoneInfo = this.businessInfo.phone;
      if (phoneInfo != null) {
        const area = phoneInfo.substring(2, 5);
        const prefix = phoneInfo.substring(5, 8);
        const rest = phoneInfo.substring(8);

        return "(" + area + ") " + prefix + "-" + rest;
      }
      return null;
    },
    isBookmarked() {
        return this.bookmarked ? true : false;
    },
    cssVars() {
      return {
        "--background-image": `url(${this.businessInfo.image_url})`,
      };
    },
  },
  methods: {
      toggleBookmark() {
          this.bookmarked = !this.bookmarked;
          userService.updateFavorite(this.$store.state.user.id,
            this.businessInfo.id, this.businessInfo.name + ' ' + this.businessInfo.location.address1, this.bookmarked, this.$store.state.token).then( resp => {
                if (resp.status === 200) {
                    this.$store.commit('SET_MESSAGE_INFO', { message: 'Favorite updated.', type: 'SUCCESS'});
                } else {
                    alert("Unable to update Favorite..");
                    this.bookmarked = !this.bookmarked;
                }
            }).catch( () => {
              this.$store.commit('SET_MESSAGE_INFO', { message: 'Favorite Unable to update Favorite.', type: 'ERROR'});
              this.bookmarked = !this.bookmarked;
            });
      }
  },
  created() {
    this.$store.commit('CLEAR_MSGS');
      userService.userHasFavorite(this.$store.state.user.id, this.businessInfo.id,
        this.$store.state.token).then( resp => {
            this.bookmarked = resp.data;
        })
  }
};
</script>

<style scoped>
img {
  padding: 20px;
}

.info-field {
  margin-top: 10px;
  color: var(--list-text-color);
}

.info-is-closed {
  color: red;
  font-size: 24px;
}

.bold {
  font-weight: bold;
}

.info-section {
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  background-image: var(--background-image);
  width: 684px;
  height: 684px;
  font-family: var(--detail-section-font-family);
}

.detail-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--detail-section-bg-color);
    width: 400px;
    padding: 20px 0;
}

.bookmark {
    font-size: 20px;
}

.green {
    color: green;
}

#name-info {
  display: flex;
  justify-content: center;
  width: 100%;
}

#name-text {
  flex-basis: 80%;
  text-align: center;
}
#mark-icon {
  justify-self: flex-end;
}


#address-info {
  grid-area: 'address-info'
}


#city-zip-info {
  grid-area: 'name-info'
}

#not-found {
  grid-area: 'not-found';
}


#phone-info {
  grid-area: 'phone-info'
}

</style>