<template>
  <div id="login" class="text-center">
    <form class="form-signin" @submit.prevent="login">
      <!-- <h1 class="h3 mb-3 font-weight-normal">Please Sign In</h1> -->
      <div id="login-section">
      <span class="login-prompt login-field">Please Sign In</span>
      <div class="alert alert-danger" role="alert" v-if="invalidCredentials">
        Invalid username and password!
      </div>
      <div
        class="alert alert-success"
        role="alert"
        v-if="this.$route.query.registration"
      >
        Thank you for registering, please sign in.
      </div>

      <span class="login-field">
      <label for="username" class="sr-only">Username</label>
      <input
        type="text"
        id="username"
        class="form-control"
        placeholder="Username"
        v-model="user.username"
        required
        autofocus
      />
      </span>
      <span class="login-field">
      <label for="password" class="sr-only">Password</label>
      <input
        type="password"
        id="password"
        class="form-control"
        placeholder="Password"
        v-model="user.password"
        required
      />
      </span>
      <span id="login-link-section" class="login-field login-link">
        <router-link class="" :to="{ name: 'register' }"
          ><span class="link-type">Need an account?</span></router-link
        >
      </span>
      <button type="submit">Sign in</button>
      </div>
    </form>
  </div>
</template>

<script>
import authService from "../services/AuthService";

export default {
  name: "login",
  components: {},
  data() {
    return {
      user: {
        username: "",
        password: "",
      },
      invalidCredentials: false,
    };
  },
  methods: {
    login() {
      authService
        .login(this.user)
        .then((response) => {
          if (response.status == 200) {
            this.$store.commit("SET_AUTH_TOKEN", response.data.token);
            this.$store.commit("SET_USER", response.data.user);
            this.$store.commit("SET_USER_PROFILE", null);
            this.$router.push({ name: "search" });
          }
        })
        .catch((error) => {
          const response = error.response;

          if (response.status === 401) {
            this.invalidCredentials = true;
          }
        });
    },
  },
};
</script>

<style scoped>

link-style {
  font-family: "Rubik Bubbles", Arial, Helvetica, sans-serif;
  color: var(--prompt-color);
}


#login-section {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 20px;
}
.login-field {
  padding: 0 5px;
}

.login-prompt {
  color: var(--prompt-color);
  font-family: "Rubik Bubbles", Arial, Helvetica, sans-serif;
  font-size: 24px;
}

.login-link {
  color: var(--prompt-color) !important;
}


.form-signin {

  background-color: var(--search-section-bg-color);
  height: 300px;
}

#login-link-section a, #login-link-section a:visited {
  color: var(--prompt-color) !important;
  font-family: "Rubik Bubbles", Arial, Helvetica, sans-serif;
  text-decoration: none;
  font-size: 24px;
}

#login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}
</style>