<template>
  <div>
    <h2>{{ headerText }}</h2>
    <div>
      <form @submit.prevent="save">
        <div class="form-element">
          <label for="first_name">First Name: </label>
          <input id="first_name" type="text" v-model="profile.firstName" :disabled="isEditZip"/>
        </div>
        <div class="form-element">
          <label for="last_name">Last Name:</label>
          <input id="last_name" type="text" v-model="profile.lastName" :disabled="isEditZip"/>
        </div>
        <div class="form-element">
          <label for="email">Email Address</label>
          <input id="email" type="email" v-model="profile.email" :disabled="isEditZip"/>
        </div>
        <div class="form-element">
          <label for="zip_code">Search Zip Code: </label>
          <input id="zip_code" type="text" v-model="profile.searchZip" />
        </div>
        <div class="form-element">
          <button>{{ saveUpdateBtnText }}</button>
          <button @click.prevent="cancel">Cancel</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import userService from "@/services/UserService";
export default {
  name: "user-profile-form",
  data() {
    return {
      profile: {},
    };
  },
  created() {
    if (this.$store.state.userProfile) {
      this.profile = this.$store.state.userProfile;
    }
  },
  computed: {
    isEdit() {
      return this.$store.state.userProfile;
    },
    isEditZip() {
      return this.$route.query && this.$route.query.editzip ? true : false;
    },
    saveUpdateBtnText() {
      return this.isEdit ? "Update" : "Save";
    },
    headerText() {
      return this.isEdit ? "Update Profile:" : "Pleasse Create Your Profile:";
    },
  },
  methods: {
    save() {
      this.profile.userId = this.$store.state.user.id;
      const data = this.profile;
      userService
        .saveProfile(data, this.isEdit, this.$store.state.token, )
        .then((resp) => {
          this.$store.commit("SET_USER_PROFILE", resp.data);
          this.$store.commit("CLEAR_LAST_SEARCH")
          this.$router.push({ name: "search" });
        });
    },
    cancel() {
      if (this.isEdit) {
        this.$router.push({ name: "search" });
      } else {
        this.$router.push({ name: "logout" });
      }
    },
  },
};
</script>

