<template>
  <div class="list-section">
    <div v-if="isLoading">
      <img src="@/assets/Fight_for_the_net.gif" />
    </div>
    <div v-if="!isLoading">
      <div class="list-item" v-for="(fave, index) in favorites" :key="index">
        <router-link
          class="item-link"
          :to="{
            name: 'business-details',
            params: { businessId: fave.yelpId },
          }"
          >{{ fave.businessName }}</router-link
        >
      </div>
    </div>
  </div>
</template>

<script>
import userService from "@/services/UserService";
export default {
  name: "user-favorites-list",
  data() {
    return {
      favorites: [],
      isLoading: true
    };
  },
  created() {
    this.$store.commit('CLEAR_MSGS');
    userService
      .getFavorites(this.$store.state.user.id, this.$store.state.token)
      .then((resp) => {
        this.favorites = resp.data;
        this.isLoading = false;
      });
  },
};
</script>

