import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

/*
 * The authorization header is set for axios when you login but what happens when you come back or
 * the page is refreshed. When that happens you need to check for the token in local storage and if it
 * exists you should set the header so that it will be attached to each request
 */
const currentToken = localStorage.getItem('token')
const currentUser = JSON.parse(localStorage.getItem('user'));

if(currentToken != null) {
  axios.defaults.headers.common['Authorization'] = `Bearer ${currentToken}`;
}

export default new Vuex.Store({
  state: {
    token: currentToken || '',
    user: currentUser || {},
    userProfile: null,
    lastSearchTerm: null,
    lastSearchResults: null,
    statusMsg: null,
    statusType: null
  },
  mutations: {
    SET_AUTH_TOKEN(state, token) {
      state.token = token;
      localStorage.setItem('token', token);
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
    },
    SET_USER(state, user) {
      state.user = user;
      localStorage.setItem('user',JSON.stringify(user));
    },
    SET_USER_PROFILE(state, profile) {
      state.userProfile = profile;
    },
    SET_LAST_SEARCH(state, searchInfo) {
      state.lastSearchTerm = searchInfo.searchTerm;
      state.lastSearchResults = searchInfo.searchResults;
    },
    CLEAR_LAST_SEARCH(state) {
      state.lastSearchTerm = null;
      state.lastSearchResults = null;
    },
    SET_MESSAGE_INFO(state, messageInfo) {
      this.state.statusMsg = messageInfo.message,
      this.state.statusType = messageInfo.type
    },
    CLEAR_MSGS(state) {
      state.statusMsg =  null;
      state.statusType = null;
    },
    CLEAR_ALL(state) {
      state.statusMsg =  null;
      state.statusType = null;
      state.lastSearchTerm = null;
      state.lastSearchResults = null;
    },
    LOGOUT(state) {
      localStorage.removeItem('token');
      localStorage.removeItem('user');
      state.token = '';
      state.user = {};
      state.userProfile = null;
      state.lastSearchTerm = null;
      state.lastSearchResults = null;
      axios.defaults.headers.common = {};
    }
  }
})
