<script>
export default {
  computed: {
    userProfile() {
      return this.$store.state.userProfile;
    },
  },
  methods: {
    logout() {
      this.$router.push({ name: "logout" });
    },
    clearSearch() {
      if (this.$route.name == "home") {
        this.$store.commit("CLEAR_ALL");
      }
    },
  },
};
</script>

