<template>
  <div id="search-pane" >
    <div class="search-area-grid">
      <!-- <label class="search-prompt search-field" for="search">Search For: </label> -->

      <label class="search-prompt search-field" for="search">Search For: </label>
      <input id="search" class="search-field search-input"  type="text"
        @keyup="keyPressed($event)" v-model="searchText" required />
      <button class="search-field search-button" @click="search">Search</button>
    </div>

    <div v-if="hasStatusMessage != null">
      <message-pane  />
    </div>

    <div id="result-section" v-show="hasStatusMessage == null">
      <businesses-list
        v-if="!isLoading && businesses != null && !notFound"
        :businesses="businesses"
      />
      <waiting-indicator class="search-waiting" v-if="isLoading" />
      
    </div>
  </div>
</template>

<script>
import fusionService from "@/services/FusionService";
import userService from "@/services/UserService";
import WaitingIndicator from "./WaitingIndicator.vue";
import BusinessesList from "./BusinessesList.vue";
import MessagePane from "./MessagePane.vue";
export default {
  components: { BusinessesList, WaitingIndicator, MessagePane },
  name: "business-search",
  data() {
    return {
      searchText: this.$store.state.lastSearchTerm,
      businessInfo: null,
      notFound: false,
      isLoading: false,
    };
  },
  watch: {
    searchTerm(newTerm, oldTerm) {
      if (newTerm != oldTerm) {
        this.searchText = newTerm;
      }
    },
  },
  computed: {
    businesses() {
      return this.$store.state.lastSearchResults;
    },
    searchTerm() {
      return this.$store.state.lastSearchTerm;
    },
    hasStatusMessage() {
      return this.$store.state.statusMsg;
    },
  },
  methods: {
    search() {
      if (this.searchText && this.searchText != "") {
        this.$store.commit('CLEAR_MSGS');
        this.isLoading = true;
        this.notFound = false;
        const term = this.searchText;
        fusionService
          .search(this.searchText, this.$store.state.userProfile.searchZip)
          .then((resp) => {
            this.isLoading = false;
            this.$store.commit("SET_LAST_SEARCH", {
              searchTerm: term,
              searchResults: resp.data,
            });
          })
          .catch((error) => {
            if (error.response && error.response.status === 404) {
              this.notFound = true;
              this.isLoading = false;
              this.$store.commit("SET_ERROR_MSG", "Business not found.");
              this.$store.commit("SET_MESSAGE_INFO", {
                message: 'Business not found.',
                type: 'ERROR' });
            } else if (error.code && error.code === 'ECONNABORTED') {
              this.$store.commit("SET_MESSAGE_INFO", {
                message: 'Unable to connect to data server.',
                type: 'ERROR' });
            }
          });
      } else {
        // this.$store.commit("SET_ERROR_MSG", "You must enter a search term.");
        this.$store.commit("SET_MESSAGE_INFO", {
          message: 'You must enter a search term.',
          type: 'ERROR'} );
      }
    },
    keyPressed(event) {
      if (event.key === "Enter") {
        this.search();
      }
    }
  },
  created() {
    if (
      this.$store.state.userProfile == null ||
      this.$store.state.userProfile == ""
    ) {
      userService
        .getProfile(this.$store.state.user.id, this.$store.state.token)
        .then((resp) => {
          if (resp.data) {
            this.$store.commit("SET_USER_PROFILE", resp.data);
          } else {
            this.$router.push({
              name: "setup",
              params: { userId: this.$store.state.user.id },
            });
          }
        });
    }
  },
};
</script>

<style>
