<template>
  <div id="register" class="text-center">
    <form class="form-register" @submit.prevent="register">
      <!-- <h1 class="h3 mb-3 font-weight-normal">Create Account</h1> -->
      <div id="register-section">
        <span class="register-prompt register-field">Create Account</span>
        <div class="alert alert-danger" role="alert" v-if="registrationErrors">
          {{ registrationErrorMsg }}
        </div>
        <span class="register-field">
          <label for="username" class="sr-only">Username</label>
          <input
            type="text"
            id="username"
            class="form-control"
            placeholder="Username"
            v-model="user.username"
            required
            autofocus
          />
        </span>
        <span class="register-field">
          <label for="password" class="sr-only">Password</label>
          <input
            type="password"
            id="password"
            class="form-control"
            placeholder="Password"
            v-model="user.password"
            required
          />
        </span>
        <span class="register-field">
          <input
            type="password"
            id="confirmPassword"
            class="form-control"
            placeholder="Confirm Password"
            v-model="user.confirmPassword"
            required
          />
        </span>
        <span class="register-field">
          <span id="register-link-section" class="register-field register-link">
            <router-link class="" :to="{ name: 'login' }"
              ><span class="link-type">Have an account?</span></router-link
            >
          </span>
          <!-- <router-link :to="{ name: 'login' }">Have an account?</router-link> -->
          <button class="btn btn-lg btn-primary btn-block" type="submit">
            Create Account
          </button>
        </span>
      </div>
    </form>
  </div>
</template>

<script>
import authService from "../services/AuthService";

export default {
  name: "register",
  data() {
    return {
      user: {
        username: "",
        password: "",
        confirmPassword: "",
        role: "user",
      },
      registrationErrors: false,
      registrationErrorMsg: "There were problems registering this user.",
    };
  },
  methods: {
    register() {
      if (this.user.password != this.user.confirmPassword) {
        this.registrationErrors = true;
        this.registrationErrorMsg = "Password & Confirm Password do not match.";
      } else {
        authService
          .register(this.user)
          .then((response) => {
            if (response.status == 201) {
              authService
                .login(this.user)
                .then((response) => {
                  if (response.status == 200) {
                    this.$store.commit("SET_AUTH_TOKEN", response.data.token);
                    this.$store.commit("SET_USER", response.data.user);
                    this.$store.commit("SET_USER_PROFILE", null);
                    this.$router.push({ name: "home" });
                  }
                })
                .catch((error) => {
                  const response = error.response;

                  if (response.status === 401) {
                    this.invalidCredentials = true;
                  }
                });

              this.$router.push({ name: "home" });
            }
          })
          .catch((error) => {
            const response = error.response;
            this.registrationErrors = true;
            if (response.status === 400) {
              this.registrationErrorMsg = "Bad Request: Validation Errors";
            }
          });
      }
    },
    clearErrors() {
      this.registrationErrors = false;
      this.registrationErrorMsg = "There were problems registering this user.";
    },
  },
};
</script>

<style>
.form-register {
  background-color: var(--search-section-bg-color);
  height: 300px;
  display: flex;
  align-items: center;
  padding: 0 20px;
}

.register-prompt {
  color: var(--prompt-color);
  font-family: "Rubik Bubbles", Arial, Helvetica, sans-serif;
  font-size: 24px;
}

.register-field {
  padding: 0 5px;
}

#register-link-section a,
#register-link-section a:visited {
  color: var(--prompt-color) !important;
  font-family: "Rubik Bubbles", Arial, Helvetica, sans-serif;
  text-decoration: none;

  font-size: 24px;
}

#register {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}
</style>
