import axios from 'axios';

const options = { timeout: 3000 };
export default {
    search(text, searchZip) {
        return axios.get(`/businesses/find/all?searchTerm=${text}&searchZip=${searchZip}`, options);
    },
    getBusinessDetails(businessId) {
        return axios.get(`/businesses/${businessId}`, options);
    },
    getReviews(businessId) {
        return axios.get(`/businesses/${businessId}/reviews`, options);
    }
}