import axios from 'axios';

export default {

    getFavorites(userId, token) {
        const headers = {
            headers: {
              Authorization: 'Bearer ' + token
            }
        }
        return axios.get(`/users/${userId}/favorites`, headers);
    },
    saveProfile(userProfile, isEdit, token) {
        const headers = {
            headers: {
              Authorization: 'Bearer ' + token
            }
        }
        if (isEdit) {
            return axios.put(`/users/${userProfile.userId}/profile`, userProfile, headers)
        } else {
            return axios.post(`/users/${userProfile.userId}/profile`, userProfile, headers)
        }
    },
    getProfile(userId, token) {
        const headers = {
            headers: {
              Authorization: 'Bearer ' + token
            }
        }
        return axios.get(`/users/${userId}/profile`, headers);
    },
    updateFavorite(userIdVal, yelpIdVal, businessNameVal, boomarked, token) {
        const headers = {
            headers: {
              Authorization: 'Bearer ' + token
            }
        }
        if (boomarked) {
            const data = {
                userId: userIdVal,
                yelpId: yelpIdVal,
                businessName: businessNameVal
            }
            return axios.post(`/users/${userIdVal}/favorites`, data, headers);
        } else {
            return axios.delete(`/users/${userIdVal}/favorites/${yelpIdVal}`, headers)
        }
    },
    userHasFavorite(userId, yelpId, token) {
        const headers = {
            headers: {
              Authorization: 'Bearer ' + token
            }
        }
        return axios.get(`/users/${userId}/favorites/${yelpId}/exists`, headers);
    }
}